header {
  background: url(../img/headerbg_01.jpg) no-repeat center center / cover;
}
header .header_top {
  width: 100%;
  background-color: rgba(240, 210, 137, 0.4);
  padding: 10px 0px;
}
header .header_top .navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
header .header_top .navbar .logo a {
  font-weight: 700;
  font-size: 30px;
  color: white;
  text-decoration: none;
  transition: 500ms;
  cursor: pointer;
}
header .header_top .navbar .logo a:hover {
  color: #fce38a;
  text-shadow: 2px 2px 2px black;
}
@media (max-width: 768px) {
  header .header_top .navbar .logo a {
    font-size: 20px;
  }
}
@media (max-width: 576px) {
  header .header_top .navbar .logo a {
    font-size: 20px;
  }
}
header .header_top .navbar ul {
  display: flex;
}
header .header_top .navbar ul li {
  margin: 0px 30px;
}
header .header_top .navbar ul li a,
header .header_top .navbar ul li i {
  text-transform: uppercase;
  text-decoration: none;
  font-size: 14px;
  font-weight: 400;
  color: white;
  display: block;
  transition: 500ms;
  cursor: pointer;
}
header .header_top .navbar ul li a:hover,
header .header_top .navbar ul li i:hover {
  color: #fce38a;
  text-shadow: 2px 2px 2px black;
}
header .header_top .navbar ul li a:hover::after,
header .header_top .navbar ul li i:hover::after {
  content: "";
  display: block;
  width: 100%;
  border-bottom: 2px solid #fce38a;
  margin-top: 10px;
}
header .header_top .navbar .bars {
  display: none;
}
header .header_top .navbar .bars i {
  color: white;
}
header .header_title {
  margin-top: 20%;
  padding-bottom: 10%;
}
header .header_title h2 {
  text-align: center;
  text-transform: capitalize;
  font-size: 60px;
  margin-bottom: 60px;
  color: white;
}
header .header_title h1 {
  font-size: 100px;
  font-weight: 700;
  text-transform: uppercase;
  color: white;
  text-align: center;
}
header .header_title h1 span {
  display: block;
}
header .header_title h1 span::after {
  content: "";
  display: block;
  width: 50px;
  height: 3px;
  background-color: white;
  margin: auto;
  margin-top: 60px;
}
header .header_title .more {
  color: white;
  margin: auto;
  margin-top: 50px;
  text-transform: uppercase;
  padding: 13px 30px;
  background-color: transparent;
  border: 2px solid white;
  display: block;
}
section .about {
  padding-top: 100px;
}
section .about p {
  text-align: center;
  line-height: 30px;
  font-weight: 400;
  font-size: 15px;
  color: #999999;
}
section .about_static {
  background-color: #95e1d3;
  margin-top: 100px;
}
section .about_static .item_number {
  padding: 0px 35px;
  border-left: 2px solid white;
}
section .about_static .item_number .number {
  padding-top: 90px;
  padding-bottom: 30px;
  font-weight: 700;
  font-size: 72px;
  color: white;
  text-align: center;
}
section .about_static .item_number .text {
  text-transform: uppercase;
  color: white;
  text-align: center;
  font-size: 14px;
  font-weight: 400;
  padding-bottom: 90px;
  line-height: 0px;
}
section .amazing_service {
  padding-top: 100px;
}
section .amazing_service .text_box {
  display: flex;
  margin-bottom: 100px;
}
section .amazing_service .text_box .icon_box {
  margin-right: 30px;
}
section .amazing_service .text_box .icon_box i {
  font-size: 30px;
  color: #95e1d3;
}
section .amazing_service .text_box .text h4 {
  text-transform: uppercase;
  font-size: 14px;
  font-weight: 400;
  margin-bottom: 25px;
}
section .amazing_service .text_box .text p {
  text-align: left;
}
section .unique_design {
  background: url(../img/09_02.jpg) no-repeat center center / cover;
  padding-top: 100px;
}
section .unique_design .box_img {
  position: relative;
  text-align: center;
}
section .unique_design .box_img .iphone {
  position: absolute;
  bottom: -76px;
  right: 14%;
}
section .what_we_do {
  padding-top: 100px;
}
section .what_we_do .we_do_title {
  text-align: center;
  line-height: 30px;
  font-weight: 400;
  font-size: 15px;
  color: #999999;
}
section .what_we_do .we_do_title span {
  display: block;
  margin-bottom: 100px;
}
section .what_we_do .tabs {
  border: 1px solid #999999;
  margin-bottom: 15px;
  padding: 20px 0px;
}
section .what_we_do .tabs .title_tabs {
  display: flex;
  justify-content: space-between;
  padding: 15px;
  align-items: center;
}
section .what_we_do .tabs .title_tabs p {
  text-transform: uppercase;
  color: black;
  font-size: 14px;
  font-weight: 400;
  display: flex;
  align-items: center;
}
section .what_we_do .tabs .title_tabs p i {
  color: #95e1d3;
  font-size: 25px;
  margin-right: 10px;
}
section .what_we_do .tabs .title_tabs .fa-arrow-to-top {
  font-size: 25px;
  color: #999999;
}
section .what_we_do .tabs .texts {
  padding: 10px;
  overflow: scroll;
  height: 100px;
  line-height: 30px;
  color: #999999;
  font-size: 14px;
  font-weight: 300;
  display: none;
}
section .active {
  display: block !important;
}
section .mt_100 {
  margin-top: 100px;
}
footer {
  margin-top: 130px;
  background-color: black;
}
@media (max-width: 992px) {
  footer {
    margin-top: 100px;
  }
}
footer .footer_top {
  background-color: #ef5050;
  padding: 40px 0px;
}
footer .footer_bottom {
  padding: 100px 0px 80px;
}
@media (max-width: 576px) {
  footer .footer_bottom {
    padding-top: 50px;
  }
}
footer .footer_bottom .logo_box {
  display: flex;
}
@media (max-width: 576px) {
  footer .footer_bottom .logo_box {
    justify-content: center;
  }
}
footer .footer_bottom .logo_box .logo_img {
  max-width: 100%;
  height: auto;
  margin-right: 20px;
}
@media (max-width: 992px) {
  footer .footer_bottom .logo_box .logo_img {
    margin-right: 5px;
  }
}
footer .footer_bottom .logo_box .logo {
  font-weight: 700;
  color: #fff;
  font-size: 30px;
  text-transform: uppercase;
  text-decoration: none;
}
footer .footer_bottom .logo_box .logo span:first-child {
  font-size: 30px;
  color: #ef5050;
}
footer .footer_bottom .logo_box .logo .logo_bottom {
  font-size: 12px;
  color: #8a8a8a;
  display: block;
  text-transform: none;
  margin-bottom: 40px;
}
footer .footer_bottom p {
  font-weight: 400;
  font-size: 13px;
  color: #bebebe;
}
@media (max-width: 576px) {
  footer .footer_bottom p {
    text-align: center;
  }
}
footer .footer_bottom .footer_social {
  margin-top: 40px;
}
@media (max-width: 576px) {
  footer .footer_bottom .footer_social {
    margin-bottom: 20px;
  }
}
@media (max-width: 576px) {
  footer .footer_bottom .footer_social {
    text-align: center;
  }
}
footer .footer_bottom .footer_social a {
  text-decoration: none;
  color: #bebebe;
  margin: 0px 10px;
}
footer .footer_bottom .footer_social a:hover {
  color: #ef5050;
  text-shadow: 2px 2px 2px white;
}
footer .footer_bottom h2 {
  font-size: 18px;
  font-weight: 400;
  color: #fff;
  text-transform: capitalize;
  margin-bottom: 30px;
}
@media (max-width: 576px) {
  footer .footer_bottom h2 {
    text-align: center;
  }
}
footer .footer_bottom .footer_news {
  display: flex;
  justify-content: space-between;
}
footer .footer_bottom .footer_news .footer_img {
  max-width: 100%;
  height: auto;
  width: 60%;
}
@media (max-width: 992px) {
  footer .footer_bottom .footer_news .footer_img {
    width: 30%;
  }
}
footer .footer_bottom .footer_news .footer_text {
  margin-left: 20px;
}
footer .footer_bottom .footer_news .footer_text span {
  color: #ef5050;
  font-size: 12px;
  font-style: italic;
  padding-top: 15px;
  display: block;
  margin-bottom: 20px;
}
@media (max-width: 576px) {
  footer .footer_bottom .footer_links {
    text-align: center;
  }
}
footer .footer_bottom .footer_links li {
  margin: 13px 0;
}
footer .footer_bottom .footer_links li a {
  text-decoration: none;
  font-size: 13px;
  font-weight: 400;
  color: #bebebe;
  text-transform: capitalize;
  transition: 5ms;
}
footer .footer_bottom .footer_links li a:hover {
  color: #ef5050;
}
footer .footer_bottom .gallery {
  margin-bottom: 45px;
  width: 100%;
  display: flex;
}
footer .footer_bottom .gallery img {
  margin: 0px 5px;
  max-width: 100%;
  width: 20%;
}
@media (max-width: 768px) {
  footer .footer_bottom .gallery img {
    width: 15%;
  }
}
footer .footer_bottom .footer_forms {
  margin-top: 23px;
}
footer .footer_bottom .footer_forms input {
  font-size: 13px;
  color: #bebebe;
  text-transform: capitalize;
  padding: 15px 0px 15px 20px;
  width: 80%;
  position: relative;
  border: none;
}
footer .footer_bottom .footer_forms .send {
  position: absolute;
  padding: 15px;
  font-size: 13px;
  background-color: #ef5050;
  border: none;
  color: white;
}
footer .footer_end {
  border-top: 2px solid #2a2828;
  padding: 30px 0px;
  text-align: center;
  text-transform: capitalize;
  color: #474747;
  font-size: 12px;
  font-weight: 700;
}
footer .footer_end span {
  color: #ff4848;
  text-transform: uppercase;
}
img {
  max-width: 100%;
  height: auto;
}
body {
  font-family: 'Montserrat', sans-serif;
}
body .click_top {
  position: fixed;
  right: 90px;
  display: none;
  top: 300px;
  z-index: 111;
}
@media (max-width: 576px) {
  body .click_top {
    right: 10px;
    top: 100px;
  }
}
body .click_top .scroll_top {
  color: #ff4848;
  background-color: transparent;
  font-size: 40px;
  border-radius: 5px;
  border: none;
  text-shadow: 2px 2px 2px black;
}
@media (max-width: 576px) {
  body .click_top .scroll_top {
    font-size: 30px;
  }
}
body .btn {
  padding: 15px 20px;
  background-color: #ff4848;
  border: none;
  text-transform: uppercase;
  font-weight: 700;
  font-size: 13px;
  text-align: center;
}
body .title {
  font-size: 24px  !important;
  font-style: italic;
  font-weight: 400;
  color: black !important ;
  text-transform: capitalize;
  text-align: center;
  margin-bottom: 30px;
}
body h3 {
  font-size: 30px;
  font-weight: 700;
  color: black;
  text-transform: uppercase;
  text-align: center;
  margin-bottom: 45px;
}
body h3::after {
  content: "";
  display: block;
  border-bottom: 3px solid #f38181;
  width: 10%;
  margin: auto;
  margin-top: 40px;
}
