@import "./header.less";
@import "./section.less";
@import "./params/var.less";
@import "./params/mix.less";
@import "./footer.less";

img {
  max-width: 100%;
  height: auto;
}

body {
  font-family: 'Montserrat', sans-serif;
  
  .click_top {
    position: fixed;
    right: 90px;
    display: none;
    top: 300px;
    z-index: 111;
    .sm-block({
        right: 10px;
        top: 100px;
    });
    .scroll_top {
      color: #ff4848;
      background-color: transparent;
      font-size: 40px;
      border-radius: 5px;
      border: none;
      text-shadow: 2px 2px 2px black;
      .sm(font-size, 30px);
    }
  }
  .btn{
    padding: 15px 20px;
    background-color: #ff4848;
    border: none;
    text-transform: uppercase;
    font-weight: 700;
    font-size: 13px;
    text-align: center;
  }
  .title{
    font-size: 24px  !important;
    font-style: italic;
    font-weight: 400;
    color: black !important ;
    text-transform: capitalize;
    text-align: center;
    margin-bottom: 30px;
  }
  h3{
    font-size: 30px;
    font-weight: 700;
    color: black;
    text-transform: uppercase;
    text-align: center;
    margin-bottom: 45px;
    &::after{
      content: "";
      display: block;
      border-bottom: 3px solid #f38181;
      width: 10%;
      margin: auto;
      margin-top: 40px;
    }
  }
}
