section {
  .about {
    padding-top: 100px;
    p {
      text-align: center;
      line-height: 30px;
      font-weight: 400;
      font-size: 15px;
      color: #999999;
    }
    
  }
  .about_static {
    background-color: #95e1d3;
    margin-top: 100px;
    .item_number {
      padding: 0px 35px;
      border-left: 2px solid white;
      .number {
        padding-top: 90px;
        padding-bottom: 30px;
        font-weight: 700;
        font-size: 72px;
        color: white;
        text-align: center;
      }
      .text {
        text-transform: uppercase;
        color: white;
        text-align: center;
        font-size: 14px;
        font-weight: 400;
        padding-bottom: 90px;
        line-height: 0px;
      }
    }
  }
  .amazing_service{
    padding-top: 100px;
    .text_box{
      display: flex;
      margin-bottom: 100px;
      .icon_box{
        margin-right: 30px;
        i{
          font-size: 30px;
          color: #95e1d3;
        }
      }
      .text{
        h4{
          text-transform: uppercase;
          font-size: 14px;
          font-weight: 400;
          margin-bottom: 25px;
        }
        p{
          text-align: left;

        }
      }
    }
  }
  .unique_design{
    background: url(../img/09_02.jpg) no-repeat center center / cover;
    padding-top: 100px;
    .box_img{
      position: relative;
      text-align: center;
      .iphone{
        position: absolute;
        bottom: -76px;
        right: 14%;

      }
    }
  }
  .what_we_do{
    padding-top: 100px;
    .we_do_title{
      text-align: center;
      line-height: 30px;
      font-weight: 400;
      font-size: 15px;
      color: #999999;
      span{
        display: block;
        margin-bottom: 100px;
      }

    }
    .tabs{
      border: 1px solid #999999;
     margin-bottom: 15px;
     padding: 20px 0px;
     .title_tabs{
       display: flex;
       justify-content: space-between;
       padding: 15px;
       align-items: center;
       
       p{
         text-transform: uppercase;
         color: black;
         font-size: 14px;
         font-weight: 400;
         display: flex;
         align-items: center;
         i{
           color: #95e1d3;
           font-size: 25px;
           margin-right: 10px;
         }
       }
       .fa-arrow-to-top{
         font-size: 25px;
         color: #999999;
       }
     }
     .texts{
       padding: 10px;
       overflow: scroll;
       height: 100px;
       line-height: 30px;
       color: #999999;
       font-size: 14px;
       font-weight: 300;
       display: none;
       
     }
    }
  }
   .active{
     display: block !important;
   }
  .mt_100 {
    margin-top: 100px;
  }
}
