header {
  background: url(../img/headerbg_01.jpg) no-repeat center center / cover;
  .header_top {
    width: 100%;
    background-color: rgba(240, 210, 137, 0.4);
    padding: 10px 0px;
    .navbar {
      display: flex;
      justify-content: space-between;
      align-items: center;
      .logo {
        a {
          font-weight: 700;
          font-size: 30px;
          color: white;
          text-decoration: none;
          transition: 500ms;
          cursor: pointer;
          &:hover {
            color: #fce38a;
            text-shadow: 2px 2px 2px black;
          }
          .md-block({
            font-size: 20px;
          });
          .sm-block({
            font-size: 20px;
          });
        }
      }
      ul {
        display: flex;
        li {
          margin: 0px 30px;
          a,
          i {
            text-transform: uppercase;
            text-decoration: none;
            font-size: 14px;
            font-weight: 400;
            color: white;
            display: block;
            transition: 500ms;
            cursor: pointer;
            &:hover {
              color: #fce38a;
              text-shadow: 2px 2px 2px black;
              &::after {
                content: "";
                display: block;
                width: 100%;
                border-bottom:  2px  solid #fce38a;
                margin-top: 10px;
              }
            }
          }
        }
      }
      .bars {
        display: none;
        i {
          color: white;
        }
      }
    }
  }
  .header_title {
    margin-top: 20%;
    padding-bottom: 10%;
    h2 {
      text-align: center;
      text-transform: capitalize;
      font-size: 60px;
      margin-bottom: 60px;
      color: white;
    }
    h1 {
      font-size: 100px;
      font-weight: 700;
      text-transform: uppercase;
      color: white;
      text-align: center;
      span {
        display: block;
        &::after {
          content: "";
          display: block;
          width: 50px;
          height: 3px;
          background-color: white;
          margin: auto;
          margin-top: 60px;
        }
      }
    }
    .more {
      color: white;
      margin: auto;
      margin-top: 50px;
      text-transform: uppercase;
      padding: 13px 30px;
      background-color: transparent;
      border: 2px solid white;
      display: block;
    }
  }
}
