footer{
    margin-top: 130px;
    background-color: black;
    .lg(margin-top, 100px);
    .footer_top{
        background-color: #ef5050;
        padding: 40px 0px;
         
    }
    .footer_bottom{
        padding: 100px 0px 80px;
        .sm(padding-top, 50px);
        .logo_box {
            display: flex;
            .sm(justify-content, center);
            .logo_img {
              max-width: 100%;
              height: auto;
              margin-right: 20px;
              .lg(margin-right, 5px );
            }
            .logo {
              font-weight: 700;
              color: #fff;
              font-size: 30px;
              text-transform: uppercase;
              text-decoration: none;
      
              span:first-child {
                font-size: 30px;
                color: #ef5050;
              }
              .logo_bottom {
                font-size: 12px;
                color: #8a8a8a;
                display: block;
                text-transform: none;
                margin-bottom: 40px;
              }
            }
          }  
          p{
              font-weight: 400;
              font-size: 13px;
              color: #bebebe;
              .sm(text-align, center);
          }
          .footer_social{
              margin-top: 40px;
              .sm(margin-bottom, 20px);
              .sm(text-align, center);
              a{
                text-decoration: none;
                color: #bebebe;
                margin: 0px 10px;    
                &:hover{
                    color: #ef5050;
                    text-shadow: 2px 2px 2px white;
                }
              }
          }
          h2{
              font-size: 18px;
              font-weight: 400;
              color: #fff;
              text-transform: capitalize;
              margin-bottom: 30px;
              .sm(text-align, center);
          }
         .footer_news{
            .footer_img {
                max-width: 100%;
                height: auto;
                width: 60%;
                .lg(width, 30%);
            }
             display: flex;
             justify-content: space-between;
              .footer_text{
                  margin-left: 20px;
                  span{
                      color: #ef5050;
                      font-size: 12px;
                      font-style: italic;
                      padding-top: 15px;
                      display: block;
                      margin-bottom: 20px;
                  }
              }
         } 
         .footer_links{
            .sm(text-align, center);
             li{
                 margin: 13px 0;
                 a{
                     text-decoration: none;
                     font-size: 13px;
                     font-weight: 400;
                     color: #bebebe;
                     text-transform: capitalize;
                     transition: 5ms;
                     &:hover{
                         color: #ef5050;
                     }
                 }
             }
         }
         .gallery{  
             margin-bottom: 45px;
             width: 100%;
             display: flex;
             
             img{
                 margin: 0px 5px;
                 max-width: 100%;
                 width: 20%;
                 .md(width, 15%);
             }
         }
         .footer_forms{
             margin-top: 23px;
             input{
                 font-size: 13px;
                 color: #bebebe;
                 text-transform: capitalize;
                 padding: 15px 0px 15px 20px;
                 width: 80%;
                 position: relative;
                 border: none;
             }
             .send{
                 position: absolute;
                 padding: 15px;
                 font-size: 13px;
                 background-color: #ef5050;
                 border: none;
                 color: white;
             }
         }

       
    }
    .footer_end{
        border-top: 2px solid #2a2828;
        padding: 30px 0px;
        text-align: center;
        text-transform: capitalize;
        color: #474747;
        font-size: 12px;
        font-weight: 700;
        span{
            color: #ff4848;
            text-transform: uppercase;
        }
    }
}